.carsSearch {
    background: #cfcfcf;
    border-radius: 20px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Rajdhani';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .carsSearch::placeholder {
    color: #adadad;
    font-family: 'Rajdhani';
    font-weight: 400;
    font-style: italic;
  }
  
  .carsSearch:focus {
    outline-color: #da7c00;
  }